import React from "react"
import { useEffect } from 'react'
import { graphql, useStaticQuery } from "gatsby"
import Image from "gatsby-image"

import TopMenu from "../components/topMenu"

import "../styles/allwork.scss"
import AnimatedText from "../components/animatedText"

let queryOeuvres;

// détermine si ça doit loop ou pas
let shouldLoop;

// stocke la position du curseur
let cursorPosX
let cursorPosY

const AllWork = () => {

  // devrait s'éxécuter quand le composant se créé
  useEffect(() => {
    shouldLoop = true;
    // ouvre les animatedTexts
    document.querySelectorAll(".carousel .animatedText").forEach((el) => {
      el.classList.add("animated")
    })


    document.addEventListener("mousemove", (e) => {
      cursorPosX = e.clientX
      cursorPosY = e.clientY
    })

    // lance la loop
    moveImages()

    // component will unmount
    return () => {
      shouldLoop = false
    }

  }, [])

  queryOeuvres = useStaticQuery(graphql`
  query oeuvresAllWork {
      allWpOeuvre {
        nodes {
          id
          title
          slug
          custom {
            bgColor
            mainimage {
              localFile {
                childImageSharp {
                  fluid {
                    tracedSVG
                    srcWebp
                    srcSetWebp
                    srcSet
                    src
                    sizes
                    presentationWidth
                    presentationHeight
                    originalName
                    originalImg
                    base64
                    aspectRatio
                  }
                }
              }
            }
          }
        }
      }
    }`)

  return (
    <div className="allworks">

      <TopMenu></TopMenu>

      <div onWheel={scollEvent} onMouseLeave={() => { resetBackground() }} className="carousel">


        {

          queryOeuvres.allWpOeuvre.nodes.map((oeuvre) => {
            const fluid = oeuvre.custom.mainimage.localFile.childImageSharp.fluid
            return (

              <div onMouseEnter={() => { changeBackground(oeuvre.custom.bgColor) }} className="carouselOeuvre" data-color={oeuvre.custom.bgColor} data-link={oeuvre.slug} onClick={(e) => { open(e) }} key={oeuvre.id}>
                <div className="carouselImageWrapper">
                  <Image fluid={fluid}></Image>
                </div>
                <h2>
                  <AnimatedText text={oeuvre.title}></AnimatedText>
                </h2>
              </div>

            )
          })

        }

        {

          queryOeuvres.allWpOeuvre.nodes.map((oeuvre) => {
            const fluid = oeuvre.custom.mainimage.localFile.childImageSharp.fluid
            return (

              <div onMouseEnter={() => { changeBackground(oeuvre.custom.bgColor) }} className="carouselOeuvre" data-color={oeuvre.custom.bgColor} data-link={oeuvre.slug} onClick={(e) => { open(e) }} key={oeuvre.id}>
                <div className="carouselImageWrapper">
                  <Image fluid={fluid}></Image>
                </div>
                <h2>
                  <AnimatedText text={oeuvre.title}></AnimatedText>
                </h2>
              </div>

            )
          })

        }

      </div>

    </div>
  )
}

const changeBackground = (color) => {
  document.documentElement.style.backgroundColor = color
}

const resetBackground = () => {
  document.documentElement.style.backgroundColor = "#e8e8e1"
}

const open = (e) => {
  // ferme les oeuvre du carousel
  let alloeuvres = document.querySelectorAll(".carousel .animatedText");
  (alloeuvres !== null) ? alloeuvres.forEach((oeuvre) => {
    oeuvre.classList.remove("animated")
  }) : console.log("nope");
  // range les barres
  let allWorksBarres = document.querySelectorAll(".carousel > div");
  (allWorksBarres !== null) ? allWorksBarres.forEach((barre) => {
    barre.classList.add("animated")
  }) : console.log("nope");
  const dest = e.target.closest(".carouselOeuvre").dataset.link;
  // la durée de l'animation est de 400ms, après ce délai on ouvre la nouvelle page
  window.setTimeout(() => {
    window.location = "/" + dest
  }, 100)
}

const scollEvent = (e) => {

  // récupère la largeur d'une oeuvre
  const oeuvreWidth = document.querySelector(".carousel div").getBoundingClientRect().width
  // récupère la largeur totale de toutes les oeuvres
  const totalWidth = oeuvreWidth * queryOeuvres.allWpOeuvre.nodes.length
  // récupère ce qui a été scroll
  const scrollLeft = document.querySelector(".carousel").scrollTop

  // si on vas trop à droite
  if (scrollLeft >= totalWidth) {
    document.querySelector(".carousel").scrollTo(0, 0)
  }

  // si on vas trop à gauche
  if (scrollLeft <= 0) {
    document.querySelector(".carousel").scrollTo(0, totalWidth)
  }

}

// fonction pour bouger les images en même temps que la souris
const moveImages = () => {
  // ne des choses que si le menu est fermé
  if (!document.querySelector(".menuIcon").classList.contains("opened")) {

    let scrollLeft = document.querySelector(".carousel").scrollTop
    const imageContainers = document.querySelectorAll(".carousel > div");
    imageContainers.forEach((el) => {
      if (el.matches(":hover")) {
        let image = el.querySelector(".carouselImageWrapper")
        image.style.top = cursorPosX + scrollLeft + "px"
        image.style.left = -cursorPosY + "px"
      }
    })

    if (shouldLoop)
      requestAnimationFrame(moveImages)

  }
}

export default AllWork